html,
body,
#root {
    height: 100%;
    width: 100%;
}

body {
  margin: 0;
  padding: 0;
}

.anim{
  opacity: 0;
  -webkit-transition: all 250ms ease-out;
  -o-transition: all 250ms ease-out;
  transition: all 250ms ease-out;
}

.in {
  opacity: 1;
}

.animated-page-wrapper {
  position: absolute;
  top: 0px;
  left: 0;
  width: 100%;
  height: 100%;
}


