.Back-Button{
  bottom: 45px;
  left: 50%;
  -webkit-transform: translateX(-50%);
      -ms-transform: translateX(-50%);
          transform: translateX(-50%);
  position: absolute;
}

.Back-Button a{
  opacity: 0.4;
  color: white;
  font-family: 'VCR OSD Mono';
  font-size: 24px;
  -webkit-text-decoration-line: none;
          text-decoration-line: none;
  -webkit-transition: opacity 250ms ease-out, letter-spacing 250ms cubic-bezier(0, 0, 0, 1);
  -o-transition: opacity 250ms ease-out, letter-spacing 250ms cubic-bezier(0, 0, 0, 1);
  transition: opacity 250ms ease-out, letter-spacing 250ms cubic-bezier(0, 0, 0, 1);
}

.Back-Button a:hover{
  letter-spacing: 5px;
  opacity: 0.8;
}

@media only screen 
  and (min-device-width: 320px) 
  and (-webkit-min-device-pixel-ratio: 2)
  and (orientation: landscape) {
  .Back-Button{
    bottom: 25px;
    left: 50%;
    -webkit-transform: translateX(-50%);
        -ms-transform: translateX(-50%);
            transform: translateX(-50%);
    position: absolute;
  }
}


